* {
  font-size: 1.03em;
}
.container {
  position: relative;
  width: 90%;
  min-height: 1.5em;
  border: 0.2em solid rgba(119, 119, 119, 0.198);
  display: flex;
  align-items: center;
  gap: 0.5em;
  padding: 0.5em;
  border-radius: 0.25em;
  outline: none;
  margin: auto;
  justify-content: center;
  margin-top: 1em;
}

.container:focus {
  border-color: hsl(200, 100%, 50%);
}

.value {
  flex-grow: 1;
  display: flex;
  gap: 0.5em;
  flex-wrap: wrap;
}

.clear-btn {
  background: none;
  color: #777;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 0;
  font-size: 2.25em;
}

.clear-btn:focus,
.clear-btn:hover {
  color: #333;
}

.divider {
  background-color: #777;
  align-self: stretch;
  width: 0.05em;
}

.caret {
  translate: 0 25%;
  border: 0.25em solid transparent;
  border-top-color: #777;
}

.options {
  position: absolute;
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
  max-height: 20em;
  overflow-y: auto;
  border: 0.2em solid rgba(119, 119, 119, 0.222);
  border-radius: 0.25em;
  width: 100%;
  left: 0;
  top: calc(100% + 0.75em);
  background-color: white;
  z-index: 100;
}

.options.show {
  display: block;
}

.option {
  padding: 0.25em 0.5em;
  cursor: pointer;
}
.Purpal {
  color: purple;
}
.Red {
  color: red;
}
.Orange {
  color: orange;
}
.Green {
  color: green;
}
.Slate {
  color: slategray;
}
.Ocean {
  color: skyblue;
}
.Blue {
  color: blue;
}
.Forest {
  color: forestgreen;
}
.Yellow {
  color: rgb(234, 234, 14);
}
.Purpal:hover {
  background-color: rgba(128, 0, 128, 0.103);
}
.Red:hover {
  background-color: rgba(255, 0, 0, 0.15);
}
.Orange:hover {
  background-color: rgba(255, 166, 0, 0.15);
}
.Green:hover {
  background-color: rgba(0, 128, 0, 0.15);
}
.Slate:hover {
  background-color: rgba(112, 128, 144, 0.15);
}
.Ocean:hover {
  background-color: rgba(14, 177, 236, 0.152);
}
.Blue:hover {
  background-color: rgba(0, 0, 255, 0.154);
}
.Forest:hover {
  background-color: rgba(34, 139, 34, 0.15);
}
.Yellow:hover {
  background-color: rgba(255, 255, 0, 0.153);
}

.option.highlighted {
  background-color: hsl(200, 100%, 50%);
  color: white;
}

.option-badge {
  display: flex;
  align-items: center;
  border: 0.05em solid #777;
  border-radius: 0.25em;
  padding: 0.15em 0.25em;
  gap: 0.25em;
  cursor: pointer;
  background: none;
  outline: none;
}

.option-badge.Purpal:hover,
.option-badge.Purpal:focus {
  background-color: rgba(128, 0, 128, 0.103);
  border-color: purple;
}
.option-badge.Red:hover,
.option-badge.Red:focus {
  background-color: rgba(255, 0, 0, 0.15);
  border-color: red;
}
.option-badge.Orange:hover,
.option-badge.Orange:focus {
  background-color: rgba(255, 166, 0, 0.15);
  border-color: orange;
}
.option-badge.Green:hover,
.option-badge.Green:focus {
  background-color: rgba(0, 128, 0, 0.15);
  border-color: green;
}
.option-badge.Slate:hover,
.option-badge.Slate:focus {
  background-color: rgba(112, 128, 144, 0.15);
  border-color: slategray;
}
.option-badge.Ocean:hover,
.option-badge.Ocean:focus {
  background-color: rgba(14, 177, 236, 0.152);
  border-color: skyblue;
}
.option-badge.Blue:hover,
.option-badge.Blue:focus {
  background-color: rgba(0, 0, 255, 0.154);
  border-color: blue;
}
.option-badge.Forest:hover,
.option-badge.Forest:focus {
  background-color: rgba(34, 139, 34, 0.15);
  border-color: forestgreen;
}
.option-badge.Yellow:hover,
.option-badge.Yellow:focus {
  background-color: rgba(255, 255, 0, 0.153);
  border-color: yellow;
}

.option-badge.Purpal > .remove-btn {
  font-size: 1.25em;
  color: purple;
  background: none;
}
