}
.option-badge.Slate > .remove-btn {
  font-size: 1.25em;
  color: slategray;
  background: none;
}
.option-badge.Ocean > .remove-btn {
  font-size: 1.25em;
  color: skyblue;
  background: none;
}
.option-badge.Blue > .remove-btn {
  font-size: 1.25em;
  color: blue;
  background: none;
}
.option-badge.Forest > .remove-btn {
  font-size: 1.25em;
  color: forestgreen;
  background: none;
}
.option-badge.Yellow > .remove-btn {
  font-size: 1.25em;
  color: yellow;
  background: none;
}
